//JavaScript Document
import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { Button } from 'antd-mobile';
 class UnsignHome extends Component {
    constructor() {
        super();
        this.state={
            degree:"120",
            money:"100"
        }
    }
    render() {
        let {degree, money}=this.state;
        return <Fragment>
           
                <div className="align-right header" onClick={this.handleDetail.bind(this)}>活动详情</div>
            
                <div className="align-center unsign-con">
                    <div className="title">签约本活动，预计全年可节省</div>
                    <div className="main">{degree}<span className="sub">度</span></div>
                    <div className="title">预计可获得电费返利</div>
                    <div  className="main">{money}<span className="sub">元</span></div>   
                    <Button type="primary"  style={{zIndex:"100", width: '1.8rem',height:'.5rem',lineHeight:".5rem",margin:".5rem auto 0",fontSize:".18rem"}}
                   onClick={this.handleSign.bind(this)}>我要签约</Button>            
                </div> 
                <div className="footer">*活动解释权归XX所有，测算数据仅供参考，以实际参与结果为准{this.props.userId}</div>
        </Fragment>;
    }
    handleSign(){
        this.props.history.push("./sign")
    }
    handleDetail(){
        this.props.history.push("./detail")
    }
};
const mapStateToProps = (state) => ({
    userId: state.userInfo.userId,
})
export default connect(mapStateToProps)(UnsignHome);